@import "https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap";
:root {
  --font-primary: "Kumbh Sans", sans-serif;
  --color-primary: #ff7d1a;
  --color-primary-1: #ffede0;
  --color-black: #000;
  --color-white: #fff;
  --color-dark-blue: #1d2025;
  --color-gray-blue: #b6bcc8;
  --color-gray-blue-dark: #68707d;
  --color-gray-blue-light: #f7f8fd;
  --border-color: #e2e7ee;
  --max-width: 125rem;
}

@media screen and (max-width: 27em) {
  :root {
    --max-width: 100%;
  }
}

*, :before, :after {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-primary);
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

.container {
  flex-direction: column;
  display: flex;
}

.header {
  width: 100%;
  max-width: var(--max-width);
  border-bottom: 1px solid var(--border-color);
  align-items: center;
  margin: 0 auto;
  padding: 2.7rem 0 2.9rem;
  display: flex;
}

@media screen and (max-width: 66em) {
  .header {
    padding-left: 5rem;
    padding-right: 5rem;
  }
}

@media screen and (max-width: 49em) {
  .header {
    padding: 2rem;
  }
}

.header__logo {
  align-items: center;
  display: flex;
}

.header__navBtn {
  justify-content: center;
  align-items: center;
  display: none;
}

.header__navBtn button {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__navBtn button svg {
  width: 1.6rem;
  height: 1.5rem;
  fill: #69707d;
}

@media screen and (max-width: 49em) {
  .header__navBtn {
    margin-right: 1.4rem;
    display: flex;
  }
}

.navigation {
  flex: 1;
  margin-left: 3.3rem;
}

.navigation__list {
  align-items: center;
  list-style: none;
  display: flex;
}

@media screen and (max-width: 49em) {
  .navigation__list {
    display: none;
  }
}

.navigation__list-item {
  margin: 0 1.7rem;
  display: block;
  position: relative;
}

.navigation__list-item a {
  color: var(--color-gray-blue-dark);
  font-size: 1.4rem;
  font-weight: 400;
  transition: all .25s;
}

.navigation__list-item:after {
  content: "";
  width: 100%;
  height: .4rem;
  visibility: hidden;
  background-color: var(--color-primary);
  transition: all .15s ease-in;
  position: absolute;
  bottom: -4.3rem;
  left: 0;
}

.navigation__list-item:hover a {
  color: var(--color-black);
}

.navigation__list-item:hover:after {
  visibility: visible;
}

.navigation__menu {
  align-items: center;
  display: flex;
}

.navigation__cart {
  margin-right: 4rem;
  position: relative;
}

@media screen and (max-width: 24em) {
  .navigation__cart {
    margin-right: 2rem;
  }
}

.navigation__cart input[type="checkbox"] {
  display: none;
}

.navigation__cart input[type="checkbox"]:checked + label + div {
  display: flex;
}

.navigation__cart label {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.navigation__cart label span {
  width: 2rem;
  height: 2rem;
  color: #fff;
  background-color: var(--color-primary);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  display: flex;
  position: absolute;
  top: -.9rem;
  right: -1rem;
}

.navigation__cart label div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.navigation__cart label div svg {
  height: 2rem;
  width: 2.2rem;
  fill: #69707d;
  fill-rule: nonzero;
}

.navigation__profile {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 50%;
  overflow: hidden;
}

@media screen and (max-width: 24em) {
  .navigation__profile {
    width: 3.4rem;
    height: 3.4rem;
  }
}

.navigation__profile button {
  border: 2.4px solid #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.navigation__profile button:hover {
  border: 2.4px solid var(--color-primary);
}

.navigation__profile button img {
  width: 100%;
  height: 100%;
}

.content {
  width: 100%;
  max-width: var(--max-width);
  margin: 8.4rem auto 0;
}

@media screen and (max-width: 49em) {
  .content {
    margin-top: 8rem;
    padding-bottom: 3rem;
  }
}

@media screen and (max-width: 27em) {
  .content {
    margin-top: 3rem;
  }
}

@media screen and (max-width: 24em) {
  .content {
    margin-top: 0;
  }
}

.product {
  display: flex;
}

@media screen and (max-width: 49em) {
  .product {
    flex-direction: column;
  }
}

@media screen and (max-width: 27em) {
  .product {
    padding: 0 2rem;
  }
}

@media screen and (max-width: 24em) {
  .product {
    padding: 0;
  }
}

.product-btn--prev, .product-btn--next {
  z-index: 5;
  width: 4rem;
  height: 4rem;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
}

@media screen and (max-width: 24em) {
  .product-btn--prev, .product-btn--next {
    display: flex;
  }
}

.product-btn--prev:hover svg, .product-btn--next:hover svg {
  stroke: var(--color-primary);
}

.product-btn--prev svg, .product-btn--next svg {
  fill: none;
  stroke-width: 3px;
  stroke: #1d2026;
  fill-rule: evenodd;
  transition: all .2s;
}

.product-btn--prev {
  left: 1.5rem;
}

.product-btn--prev svg {
  width: 1.2rem;
  height: 1.8rem;
}

.product-btn--next {
  right: 1.5rem;
}

.product-btn--next svg {
  width: 1.2rem;
  height: 1.8rem;
}

.product-image {
  flex-direction: column;
  flex: 1;
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 49em) {
  .product-image {
    flex-direction: row;
    justify-content: center;
    gap: 3rem;
    margin-bottom: 6rem;
  }
}

@media screen and (max-width: 27em) {
  .product-image {
    flex-direction: column;
    margin-bottom: 5rem;
  }
}

.product-image__wrapper {
  overflow: hidden;
}

.product-image__slider {
  width: 40rem;
  height: 40rem;
  cursor: zoom-in;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;
  align-items: center;
  transition: all .25s linear;
  display: flex;
}

@media screen and (max-width: 24em) {
  .product-image__slider {
    width: 100%;
    height: 100%;
  }
}

.product-image__slider img {
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
  transform-origin: center;
  border-radius: 1.3rem;
  display: block;
}

@media screen and (max-width: 24em) {
  .product-image__slider img {
    border-radius: 0;
  }
}

.product-image__thumbnails {
  gap: 2.6rem;
  margin-top: 3rem;
  display: flex;
}

@media screen and (max-width: 49em) {
  .product-image__thumbnails {
    flex-direction: column;
    margin-top: 0;
  }
}

@media screen and (max-width: 27em) {
  .product-image__thumbnails {
    flex-direction: row;
  }
}

@media screen and (max-width: 24em) {
  .product-image__thumbnails {
    gap: 1.2rem;
    display: none;
  }
}

.product-image__thumbnails > .active {
  border: 2.3px solid var(--color-primary);
}

.product-image__thumbnails > .active img {
  opacity: .4;
}

.product-image__thumbnails > * {
  width: 8rem;
  height: 8rem;
  cursor: pointer;
  border: 2.3px solid #0000;
  border-radius: .9rem;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
  overflow: hidden;
}

@media screen and (max-width: 24em) {
  .product-image__thumbnails > * {
    width: 100%;
    height: 100%;
  }
}

.product-image__thumbnails > * img {
  width: 100%;
  height: 100%;
  transition: all .3s;
}

.product-image__thumbnails > :hover img {
  opacity: .4;
}

.product-info {
  flex: 1;
  align-self: center;
}

@media screen and (max-width: 24em) {
  .product-info {
    padding: 0 2rem;
  }
}

.product-tag {
  margin-bottom: 1.7rem;
  display: block;
}

@media screen and (max-width: 24em) {
  .product-tag {
    margin-bottom: 1.5rem;
  }
}

.product-tag p {
  letter-spacing: .07em;
  text-transform: uppercase;
  color: var(--color-primary);
  font-size: 1.26rem;
  font-weight: 700;
}

@media screen and (max-width: 24em) {
  .product-tag p {
    font-size: 1.24rem;
  }
}

.product-heading {
  max-width: 50rem;
  margin-bottom: 2.8rem;
  display: block;
}

@media screen and (max-width: 27em) {
  .product-heading {
    margin-bottom: 2.3rem;
  }
}

.product-heading h1 {
  color: var(--color-black);
  text-transform: capitalize;
  font-size: 4rem;
  line-height: 4.4rem;
}

@media screen and (max-width: 27em) {
  .product-heading h1 {
    font-size: 3.4rem;
    line-height: 4.2rem;
  }
}

@media screen and (max-width: 20em) {
  .product-heading h1 {
    font-size: 3rem;
    line-height: 3.9rem;
  }
}

.product-description {
  max-width: 40rem;
  margin-bottom: 2.2rem;
}

.product-description p {
  color: var(--color-gray-blue-dark);
  font-size: 1.42rem;
  line-height: 2.1rem;
}

.product-value {
  flex-direction: column;
  margin-bottom: 2.5rem;
  display: flex;
}

@media screen and (max-width: 24em) {
  .product-value {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.product-value__price {
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.product-value__price p {
  color: var(--color-black);
  font-size: 2.6rem;
  font-weight: 700;
}

@media screen and (max-width: 20em) {
  .product-value__price p {
    font-size: 2.4rem;
  }
}

.product-value__price span {
  color: var(--color-primary);
  background-color: #ff7d1a2e;
  border-radius: .6rem;
  margin-left: 1.2rem;
  padding: .4rem 1rem;
  font-size: 1.4rem;
  font-weight: 700;
}

@media screen and (max-width: 20em) {
  .product-value__price span {
    font-size: 1.3rem;
  }
}

.product-value__discount {
  display: block;
}

.product-value__discount p {
  color: var(--color-gray-blue);
  font-size: 1.45rem;
  font-weight: 700;
  text-decoration: line-through;
}

.product-menu {
  display: flex;
}

@media screen and (max-width: 24em) {
  .product-menu {
    flex-direction: column;
    align-items: stretch;
    gap: 2rem;
  }
}

.product-qty-selector {
  background-color: var(--color-gray-blue-light);
  border-radius: 1rem;
  margin-right: 1.5rem;
  display: flex;
}

@media screen and (max-width: 24em) {
  .product-qty-selector {
    justify-content: space-between;
    margin-right: 0;
  }
}

.product-qty-selector span {
  justify-content: center;
  align-items: center;
  padding: 1.4rem;
  transition: all .2s;
  display: flex;
}

.product-qty-selector span:first-child {
  border-radius: 1rem 0 0 1rem;
}

.product-qty-selector span:first-child svg {
  width: 1.2rem;
  height: .4rem;
  fill: #ff7e1b;
}

.product-qty-selector span:last-child {
  border-radius: 0 1rem 1rem 0;
}

.product-qty-selector span:last-child svg {
  width: 1.2rem;
  height: 1.2rem;
  fill: #ff7e1b;
}

.product-qty-selector span:not(:nth-child(2n)) {
  cursor: pointer;
}

.product-qty-selector span:not(:nth-child(2n)):hover {
  opacity: .6;
  background-color: #f3f5fa;
}

.product-btn {
  display: flex;
}

@media screen and (max-width: 24em) {
  .product-btn {
    flex: 1;
  }
}

.product-btn button {
  color: #fff;
  background-color: var(--color-primary);
  border-radius: .8rem;
  justify-content: center;
  align-items: center;
  padding: 0 6rem;
  font-family: inherit;
  transition: all .24s;
  display: flex;
  box-shadow: 0 7px 29px #ff7d1a80;
}

.product-btn button:hover {
  opacity: .6;
}

@media screen and (max-width: 24em) {
  .product-btn button {
    width: 100%;
    padding: 1.4rem;
  }
}

.product-btn button span:first-child svg {
  height: 2rem;
  width: 2.2rem;
  fill: #fff;
  fill-rule: nonzero;
}

.product-btn button span:last-child {
  margin-left: 1.4rem;
  font-size: 1.4rem;
  font-weight: 700;
}

@media screen and (max-width: 20em) {
  .product-btn button span:last-child {
    font-size: 1.3rem;
  }
}

.num {
  color: var(--color-black);
  font-size: 1.5rem;
  font-weight: 700;
}

.cart {
  width: 100%;
  z-index: 10;
  height: 100%;
  background-color: #0000;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 9rem 15rem;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media screen and (max-width: 66em) {
  .cart {
    padding: 8rem 4rem;
  }
}

@media screen and (max-width: 27em) {
  .cart {
    justify-content: center;
    padding: 7.5rem 2rem 0;
  }
}

@media screen and (max-width: 20em) {
  .cart {
    padding: 7.5rem .5rem 0;
  }
}

.cart__empty {
  justify-content: center;
  align-items: center;
  padding: 7.62rem 10.15rem;
  display: flex;
}

@media screen and (max-width: 24em) {
  .cart__empty {
    padding-left: 10rem;
    padding-right: 10rem;
  }
}

.cart__empty p {
  color: var(--color-gray-blue-dark);
  font-size: 1.38rem;
  font-weight: 700;
}

.cart__wrapper {
  background-color: #fff;
  border-radius: .9rem;
  flex-direction: column;
  display: flex;
  box-shadow: 0 13px 27px -5px #32325d40, 0 8px 16px -8px #0000004d;
}

@media screen and (max-width: 20em) {
  .cart__wrapper {
    width: 100%;
  }
}

.cart__head {
  border-bottom: 1px solid var(--border-color);
  padding: 1.9rem 2rem;
}

.cart__head h3 {
  font-size: 1.4rem;
  font-weight: 700;
}

.cart__list {
  flex-direction: column;
  display: flex;
}

.cart__list-item {
  align-items: center;
  padding: 2.4rem 2rem 1.7rem;
  display: flex;
}

@media screen and (max-width: 20em) {
  .cart__list-item {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

.cart__list-item figure {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: .6rem;
  overflow: hidden;
}

.cart__list-item figure img {
  width: 100%;
  height: 100%;
}

.cart__list-item div {
  flex: 1;
  margin-left: 1.5rem;
  margin-right: 2rem;
}

@media screen and (max-width: 20em) {
  .cart__list-item div {
    margin-right: 0;
  }
}

.cart__list-item div p {
  white-space: nowrap;
  color: var(--color-gray-blue-dark);
  margin-bottom: .3rem;
  font-size: 1.42rem;
  font-weight: 400;
}

@media screen and (max-width: 27em) {
  .cart__list-item div p {
    white-space: wrap;
  }
}

.cart__list-item div span {
  color: var(--color-gray-blue-dark);
  font-size: 1.42rem;
  font-weight: 400;
}

.cart__list-item div span span {
  color: var(--color-black);
  margin-left: .3rem;
  font-weight: 700;
}

.cart__list-item button {
  justify-content: center;
  align-items: center;
  display: flex;
}

.cart__list-item button svg {
  width: 1.4rem;
  height: 1.6rem;
  fill: #c3cad9;
}

.cart__btn {
  flex: 1;
  padding: 1.2rem 2rem 2.4rem;
}

.cart__btn button {
  width: 100%;
  color: #fff;
  background-color: var(--color-primary);
  border-radius: .8rem;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 0;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 700;
  display: flex;
}

.sidebar {
  z-index: 50;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  position: fixed;
  top: 0;
}

.sidebar-wrapper {
  min-height: 100vh;
  width: -moz-min-content;
  width: min-content;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 .5rem 1.5rem #00000014;
}

.sidebar-wrapper.open {
  animation: .3s ease-out forwards sidedrawerOpen;
}

.sidebar-wrapper.close {
  animation: .3s ease-out forwards sidedrawerClosed;
}

.sidebar__btn {
  align-items: center;
  padding: 2rem 0 2rem 2rem;
  display: flex;
}

.sidebar__btn svg {
  width: 1.4rem;
  height: 1.5rem;
  fill: #69707d;
}

.sidebar__menu {
  margin-top: .5rem;
  display: block;
}

.sidebar__menu-list {
  flex-direction: column;
  gap: 2.1rem;
  padding: 1.5rem 15rem 1.5rem 2rem;
  list-style: none;
  display: flex;
}

.sidebar__menu-item {
  display: block;
}

.sidebar__menu-item a {
  color: var(--color-black);
  font-size: 1.6rem;
  font-weight: 700;
}

@keyframes sidedrawerOpen {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes sidedrawerClosed {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

.modal {
  z-index: 50;
  width: 100%;
  height: 100%;
  background-color: #000000ce;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

@media screen and (max-width: 27em) {
  .modal {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media screen and (max-width: 24em) {
  .modal {
    display: none;
  }
}

.modal-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 27em) {
  .modal-wrapper {
    width: 100%;
  }
}

.modal-wrapper.scaleUp {
  animation: .3s ease-out forwards scaleUp;
}

.modal-wrapper.scaleDown {
  animation: .3s ease-out forwards scaleDown;
}

@keyframes scaleUp {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
  }

  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}

@keyframes scaleDown {
  from {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }

  to {
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
  }
}

.modal-main {
  justify-content: center;
  align-items: center;
  margin-bottom: 3.5rem;
  display: flex;
  position: relative;
}

@media screen and (max-width: 24rem) {
  .modal-main {
    width: 100%;
    height: 100%;
  }
}

.modal-main__wrapper {
  overflow: hidden;
}

.modal-main__slider {
  width: 45rem;
  height: 45rem;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;
  transition: all .25s linear;
  display: flex;
}

@media screen and (max-width: 27em) {
  .modal-main__slider {
    width: 100%;
    height: 100%;
  }
}

.modal-main__slider img {
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
  transform-origin: center;
  border-radius: 1.3rem;
}

.modal-grid {
  gap: 2.6rem;
  display: flex;
}

.modal-grid > .active {
  border: 2.3px solid var(--color-primary);
}

.modal-grid > .active:after {
  background-color: #ffffff9f;
}

.modal-grid > * {
  width: 8rem;
  height: 8rem;
  cursor: pointer;
  border: 2.3px solid #0000;
  border-radius: .9rem;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.modal-grid > :hover:after {
  background-color: #ffffff9f;
}

.modal-grid > :after {
  content: "";
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: block;
  position: absolute;
}

.modal-grid > * img {
